
.footer {
  width: 100%;
  height: 300px;
  background-color: #eee;
}
.footer .footer_inner {
  width: 100%;
  height: 100%;
  padding: 60px 50px;
  box-sizing: border-box;
}
.footer .footer_inner .footer_tb {
  width: 70%;
  height: 50%;
  font-size: 13px;
  text-align: center;
  margin: 0 auto;
}
.footer .footer_inner .footer_tb a:hover {
  color: #00a1d6;
}
.footer .footer_inner .footer_bottom {
  width: 70%;
  height: 50%;
  margin: 0 auto;
  padding-top: 20px;
  font-size: 13px;
  text-align: center;
  color: #7e8c8d;
}
.footer .footer_inner .footer_bottom h1 {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 30px;
}


