
.header-wrapper {
  width: 100%;
  height: 60px;
  background-color: #fff;
  position: fixed;
  padding-left: 50px;
  border-bottom: 2px solid #ffafc9;
  z-index: 6666;
}
.header-wrapper .logo-wrapper {
  width: 60px;
  height: 100%;
  display: inline-block;
  vertical-align: top;
  padding-top: 5px;
}
.header-wrapper h1 {
  font-size: 26px;
  display: inline-block;
  height: 60px;
  line-height: 60px;
  vertical-align: top;
  color: #00a1d6;
}
.header-wrapper h1 a {
  color: #00a1d6;
}
.header-wrapper .login_reg {
  display: inline-block;
  position: absolute;
  left: 1000px;
  height: 60px;
  width: 140px;
  vertical-align: middle;
  color: #00a1d6;
  font-size: 20px;
}
.header-wrapper .login_reg .login_fade-enter,
.header-wrapper .login_reg .login_fade-leave-active {
  opacity: 0;
}
.header-wrapper .login_reg .login_fade-enter-active,
.header-wrapper .login_reg .login_fade-leave-active {
  transition: all 0.6s;
}
.header-wrapper .login_reg a {
  cursor: pointer;
  display: inline-block;
  height: 60px;
  width: 60px;
  color: #00a1d6;
  text-align: center;
  line-height: 60px;
}
.header-wrapper .login_reg a:hover {
  background: #f25d8e;
  color: #fff;
}
.header-wrapper .login_reg .login_wrapper {
  position: absolute;
  border-radius: 0 0 10px 10px;
  margin-top: 2px;
  border: 2px solid #ffafc9;
  border-top: none;
  background-color: #fff;
  padding: 20px;
  box-sizing: border-box;
  width: 300px;
  height: 200px;
  left: -50px;
  font-size: 18px;
  color: #000;
}
.header-wrapper .login_reg .login_wrapper .login {
  height: 130px;
  text-align: center;
}
.header-wrapper .login_reg .login_wrapper .login input {
  width: 100%;
  height: 30px;
  border: 1px solid rgba(0,0,0,0.2);
  font-size: 16px;
  color: #666;
  border-radius: 6px;
  text-indent: 6px;
  margin-bottom: 30px;
}
.header-wrapper .login_reg .login_wrapper .login button {
  border: none;
  width: 100px;
  height: 40px;
  font-size: 20px;
  border-radius: 10px;
  letter-spacing: 6px;
  background-color: #67bdcd;
  color: #fff;
}
.header-wrapper .userLogin {
  display: inline-block;
  position: absolute;
  left: 1000px;
  height: 60px;
  line-height: 60px;
  width: 300px;
  font-size: 14px;
  vertical-align: middle;
  color: #00a1d6;
  font-size: 20px;
}
.header-wrapper .userLogin>span:first-child {
  display: inline-block;
  width: 200px;
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.header-wrapper .userLogin>span:last-child {
  display: inline-block;
  margin-left: 20px;
  font-size: 14px;
  vertical-align: top;
}
.header-wrapper .userLogin>span:last-child a {
  color: #00a1d6;
}


