
.content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 20px auto;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 1000px;
}
.content .left {
  width: 740px;
  height: 100%;
}
.content .left h3 {
  height: 50px;
  line-height: 50px;
  font-size: 20px;
  color: #222;
  border-bottom: 1px solid #f10214;
}
.content .left .top_title {
  margin-bottom: 20px;
}
.content .left .img-wrapper {
  height: 300px;
  overflow: hidden;
  position: relative;
}
.content .left .img-wrapper .img-scroll {
  position: absolute;
  white-space: nowrap;
}
.content .left .img-wrapper .img-scroll .imgs {
  display: inline-block;
  margin-left: 12px;
  width: 170px;
}
.content .left .img-wrapper .img-scroll .imgs .mv-title {
  font-size: 16px;
  height: 30px;
  line-height: 30px;
  text-align: center;
}
.content .left .img-wrapper .scroll_bar {
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -250px;
  width: 500px;
  text-align: center;
}
.content .left .img-wrapper .scroll_bar .item {
  display: inline-block;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  margin-right: 8px;
  background-color: #999;
  cursor: pointer;
}
.content .left .img-wrapper .scroll_bar .on {
  background-color: #f10214;
}
.content .left .movie_wrapper {
  margin-top: 20px;
}
.content .left .movie_wrapper .movie_item {
  height: 200px;
  margin-bottom: 20px;
  border-bottom: 1px solid rgba(0,0,0,0.1);
  box-shadow: 0 1px 1px rgba(0,0,0,0.1);
  display: inline-block;
  width: 185px;
  text-align: center;
}
.content .right {
  width: 240px;
  margin-top: 20px;
  border: 1px solid #edf2f9;
  box-shadow: 0 1px 2px rgba(0,0,0,0.2);
  border-radius: 4px;
}
.content .right .rank_wrapper {
  padding: 20px;
}
.content .right .rank_wrapper h2 {
  padding-bottom: 10px;
  height: 40px;
  line-height: 40px;
  font-size: 24px;
  font-weight: normal;
  color: #81c2d6;
  border-bottom: 1px dashed #ff7073;
}
.content .right .rank_wrapper ul .rank_item {
  height: 60px;
  line-height: 60px;
  font-size: 20px;
  letter-spacing: 2px;
  border-bottom: 1px dashed rgba(0,0,0,0.2);
}
.content .right .rank_wrapper ul .rank_item a {
  display: block;
  width: 100%;
}
.content .right .rank_wrapper ul .rank_item:last-child {
  border-bottom: none;
}
.content .right .rank_wrapper ul .rank_item .rank_num {
  font-size: 26px;
  color: #f5b977;
  margin-right: 10px;
}
.content .right .rank_wrapper ul .rank_item .movie_title {
  display: inline-block;
  vertical-align: top;
  height: 60px;
  line-height: 60px;
}


